/* Buttons, links, and accessibility */
.github-btn,
.download-btn {
  background: var(--accent-color);
  color: var(--bg-color);
  transition: background 0.2s, color 0.2s;
}
.github-btn:hover,
.download-btn:hover {
  background: var(--bg-color);
  color: var(--accent-color);
  border: 2px solid var(--accent-color);
}
.theme-toggle {
  background: var(--card-bg);
  color: var(--accent-color);
  border: 2px solid var(--accent-color);
  border-radius: 50%;
  width: 44px;
  height: 44px;
  font-size: 1.3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 8px #0002;
  cursor: pointer;
  transition: background 0.2s, color 0.2s, border 0.2s, transform 0.2s;
  z-index: 300;
}
.theme-toggle:hover {
  background: var(--accent-color);
  color: var(--bg-color);
  border: 2px solid var(--bg-color);
  transform: scale(1.08) rotate(-10deg);
}
a,
button,
.download-btn,
.github-btn,
.theme-toggle {
  outline: none;
}
a:focus,
button:focus,
.download-btn:focus,
.github-btn:focus,
.theme-toggle:focus {
  box-shadow: 0 0 0 3px #01c16a88;
  border-radius: 6px;
  z-index: 10;
}
